          position: relative;
          display: block;
          @include cssTransition(.2s);
          &:after {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            background: rgba(239,122,35,.3);
            left: 0;
            top: 0;
            cursor: pointer; } }
        a:hover:after {
          background: rgba(239,122,35,.4); }
        img {
          width: 100%; } } } } }

.button.read-more {
